@import "../../global.scss";

.aboutme{
    background-color:white;
    display: flex;

    @include mobile{
        flex-direction: column;
        align-items: center;
    }


    .left{
        flex: 0.5;
        overflow: hidden;

        .imgContainer{
            width: 700px;
            height: 700px;
            display: flex;
            align-items: flex-end;
            justify-content: center;
            float: right;

            @include mobile{                
                align-items: flex-start;
            }     

            img{
                height: 100%;
                border-radius: 15%;
                background-color:$goldleaf;

                @include mobile{
                  height: 40%;
                }
            } 
        }
    }

    .right{
        flex: 0.5;
        position: relative;

        .wrapper{
            widows: 100%;
            height: 100%;
            padding-left: 0;
            display: flex;
            flex-direction: column;
            justify-content: center;
        }

        @include mobile{
            padding-left: 0;
            align-items: center;
        }

        h5{
            font-size: 35px;
            margin: 5px 0;
            text-align: center;
            color: $goldleaf;
            font-style: italic;
            @include mobile{                
                align-items: flex-start;
            }  
        }
    }
}
