@import "../../global.scss";

#contact {
  background-color: white;
  display: flex;
  @include mobile{
    display: flex;
    object-fit: cover;
    text-align: center;
    flex-direction: column;
  }

  .wrapper {
    flex: 1;
    overflow: hidden;
    widows: 100%;
    height: 100%;
    opacity: 0.85;
    background-color: white;
    background-repeat: no-repeat;
    display: flex;
    align-items: center;
    flex-direction: column;
    box-shadow: 0px 0px 15px -8px rgb(252, 5, 5);
    justify-content: center;
    @include mobile{                
        align-items: flex-start;
    } 
    ul{
        li{
            .icon {
                font-size: 25px;
                margin-right: 10px;
              }
            span{          
                
                font-size: 25px;      
                align-items: center;
                justify-content: center;
            }
        }
    }
  }
}
