@import "../../global.scss";

#intro{
    @include mobile{
        display: flex;
        object-fit: cover;
        text-align: center;
    }
    .item{
        flex: 1;
        position: relative;
    
        .wrapper{
            widows: 100%;
            height: 100%;
            opacity: 0.85;
            background-color: white;
            background-repeat: no-repeat;
            display: flex;
            align-items: center;
            flex-direction: column;
            box-shadow: 0px 0px 15px -8px rgb(252, 5, 5);
            justify-content: center;
            object-fit: cover;
            @include mobile{                
                align-items: flex-start;
            }   
        }
    
        h1{
            font-size: 60px;
            margin: 10px 0;
        }
        h2{
            font-size: 35px;
        }
        h3{
            font-size: 30px;
    
            span{
                font-size: inherit;
                color:$skyblue;
            }
            .ityped-cursor{
                animation: blink 1s infinite;
            }
    
            @keyframes blink {
                50%{
                    opacity: 0;
                }
                100%{
                    opacity: 0;
                }
                
            }
        }
    }
    .btn{
        top:10px;
    }
}
    